<template>

  <div>
    <a-page-header
      style="border: 1px solid rgb(235, 237, 240)"
      title="创建主题"
      @back="() => $router.go(-1)"
    />
    <div class="p-6">
      <a-form-model :model="form" :label-col="labelCol" :wrapper-col="wrapperCol" :rules="rules">
        <a-form-model-item label="标题">
          <a-input v-model="form.title" />
        </a-form-model-item>
        <a-form-model-item label="内容">
          <a-input v-model="form.content" type="textarea" />
        </a-form-model-item>
        <a-form-model-item :wrapper-col="{ span: 14, offset: 4 }">
        <a-button type="primary" @click="onSubmit">
          Create
        </a-button>
        <a-button style="margin-left: 10px;">
          Cancel
        </a-button>
      </a-form-model-item>
    </a-form-model>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  data () {
    return {
      labelCol: { span: 4 },
      wrapperCol: { span: 14 },
      form: {
        title: '',
        content: '',
        userId: this.$store.getters.getUserId
      },
      rules: {
        title: [
          { required: true, message: 'Please input Activity title', trigger: 'blur' },
          { min: 3, max: 5, message: 'Length should be 3 to 5', trigger: 'blur' }
        ],
        content: [
          { required: true, message: 'Please input Content', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    onSubmit () {
      axios.post(process.env.VUE_APP_APIPATH + process.env.VUE_APP_CREATETOPIC, { ...this.form })
        .then((response) => {
          console.log(response.data)
        })
    }
  }
}
</script>

